/*BODY*/
@import url("https://fonts.googleapis.com/css?family=Roboto");
@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro');

body {
  font-family: "Roboto", monospace;
  background-color: #eeeeee;
  padding: 0;
  margin: 0;
}

/*LAYOUT*/

div.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  padding-bottom: 30px;
  margin: 0;
}

div.container > div.header {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

div.container > div.header > h2 {
  margin-top: -5px;
  margin-bottom: -2px;
  /* border : 1px solid orange; */
}

div.container > div.nav {
  position: fixed;
  top: 0;
  left: 0;
  min-width: 20%;
  min-height: 96vh;
  background-color: #292929;
  padding: 0 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

div.container > div.main {
  width: 100%;
  min-height: 85vh;
  padding: 10px 10px 10px 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-left: 21%;
}

div.container > div.main > h2 {
  margin: 10px;
}

div.container > div.footer {
  z-index: 10;
  width: 100%;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  height: 33px;
  padding: 4px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: white;
  background-color: #292929;
  -webkit-box-shadow: 5px 1px 5px 5px rgba(0, 0, 0, 0.3);
          box-shadow: 5px 1px 5px 5px rgba(0, 0, 0, 0.3);
}
div.container > div.footer > p {
  margin: 0;
}

div.container > div.drawerContainer {
  display: none;
  z-index: 9;
}

@media (max-width: 900px) {
  div.container > div.nav {
    display: none;
  }
  div.container > div.main {
    padding-top: 60px;
    width: 100%;
    min-height: 93vh;
    padding-left: 10px;
  }
  div.container > div.main > h2 {
    margin: 0;
  }
  div.container > div.nav > ul > li {
    width: auto;
  }
  div.container > div.footer > p {
    margin: 3px;
    font-size: 0.8em;
  }
  div.container > div.drawerContainer {
    display: block;
  }
}

/*PORTFOLIO LIST*/
div.record-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
}
div.record,
li.skill {
  border: 1px solid #edfffb;
  background-color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-direction: column;
      flex-direction: column;
  line-height: 24px;
  margin: 5px;
  padding: 10px;
  width: 21%;
  border-radius: 3px;
  -webkit-box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, 0.1);
          box-shadow: 1px 1px 10px 1px rgba(0, 0, 0, 0.1);
  -webkit-transition: -webkit-box-shadow 0.4s ease-in-out;
  transition: -webkit-box-shadow 0.4s ease-in-out;
  -o-transition: box-shadow 0.4s ease-in-out;
  transition: box-shadow 0.4s ease-in-out;
  transition: box-shadow 0.4s ease-in-out, -webkit-box-shadow 0.4s ease-in-out;
}


div.record:hover,
li.skill:hover {
  -webkit-box-shadow: 1px 1px 10px 3px rgba(0, 0, 0, 0.2);
          box-shadow: 1px 1px 10px 3px rgba(0, 0, 0, 0.2);
  -webkit-transition: -webkit-box-shadow 0.4s ease-in-out;
  transition: -webkit-box-shadow 0.4s ease-in-out;
  -o-transition: box-shadow 0.4s ease-in-out;
  transition: box-shadow 0.4s ease-in-out;
  transition: box-shadow 0.4s ease-in-out, -webkit-box-shadow 0.4s ease-in-out;
}

div.record > span.column {
  width: 100%;
}

span.pDesc {
  margin-top: 5px;
}
span.pTech {
  margin-top: 5px;
  font-size: 0.8em;
}

@media (max-width: 1041px) {
  div.record {
    width: 27%;
    margin-bottom: 10px;
    font-size: 0.9em;
    padding: 7px;
    -ms-flex-positive: 1;
        flex-grow: 1;
  }

  li.skill {
    width: auto;
  }
}

@media (max-width: 600px) {
  div.record-container {
    font-size: 1em;
  }
  div.record {
    width: 100%;
    padding: 7px;
    margin: 3px;
  }

  span.pName {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding-right: 50px;
  }

  span.pDesc {
    margin-top: 0;
  }

  span.pTech {
    margin-top: 0;
  }

  span.pGithub {
    position: absolute;
    right: 20px;
    width: 50px !important;
    text-align: right;
  }

  li.skill {
    width: auto;
    font-size: 0.9em;
    padding: 5px;
  }

  div.footer {
    opacity: 0.9;
  }
}

a {
  text-decoration: none;
  color: #1fbba6;
}
a:hover {
  color: orange;
}



